import React, {useContext, useState, useRef} from 'react';
import ProductContext from "../Context/Product_Context";
import {BASE_URL, fetchPost, SHIPPING_COST} from "../Utils/Constants";
import Swal from 'sweetalert';
import { withRouter } from 'react-router-dom';
import DatePicker from "react-datepicker2";

import { registerLocale, setDefaultLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';
registerLocale('es', es)


const Cart = props => {

  const { cart, cartCount, cartTotal } = useContext(ProductContext);
  const [value, setValue] = useState('');
  const comments = useRef(null);
  const [ clicking, setClicking ]= useState("block");
  const [ showp, SetShowp ]= useState("none");
  // const dateNow = new Date().setDate(new Date().getDate() - 1);
  const dateNow = new Date().setDate(new Date().getDate());
  const [methodSelected,SetMethodSelected] = useState(11);

  console.log(dateNow);


  const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
    minimumFractionDigits: 2
  });

  const submitOrder = () => {

    const user = JSON.parse(localStorage.getItem('user'));


    if(user === null)
      Swal('','Debes de iniciar sesión para poder hacer el pedido', 'error');
    else{

      if(value === '' || value === null || value === undefined) {
        Swal('', 'Debes seleccionar la fecha del pedido', 'error');
        return;
      }

      // if(cartTotal < 500) {
      //   Swal('', 'El pedido debe ser mayor a $500.00 (sin contar el costo de envío)', 'warning');
      //   return;
      // }

      // if(methodSelected === 11) {
      //   Swal('', 'Debes seleccionar un método de pago.', 'warning');
      //   return;
      // }


      const order = {
        num_items:cartCount,
        subtotal: cartTotal,
       // shipping_cost:SHIPPING_COST,
        //total:Number(cartTotal) + Number(SHIPPING_COST),
       // status_payment:methodSelected,
      };

      const extraData = {
        comments: comments.current.value,
        delivery_data: value
      };

      const products = [...cart];
      console.log(products);
      const orderData = {
        order,
        products,
        extraData
      };

  
      const formData = new FormData();
      formData.append('order',JSON.stringify(orderData));
     // console.log(JSON.stringify(orderData));


        SetShowp("block");
        setClicking("none");

      fetchPost('registerOrder',formData, user.token)
        .then(response => {
          console.log(response);
          if(response.error) {
              Swal('', response.message, 'error');
              SetShowp("none");
              setClicking("block");
          }else{
         
              Swal('',response.message,'success');
              localStorage.removeItem('cart');
              localStorage.removeItem('count');
              localStorage.removeItem('total');
              setTimeout(()=> {
                window.location.href = '/';
              },1000);
            
          }
        }).catch(error =>{
          SetShowp("none");
          setClicking("block");
          console.log(error);
      });
    }
  };

  const BtnCart = () => {
    return (
      cartCount > 0 ? (
        <div className="cartBtn2">

            <div style={{display:clicking}}>
                <button type="button" className="btn btnTheme" onClick={() => submitOrder(cart, cartCount)}> Levantar pedido </button>
            </div>
            <div style={{display:showp}}>
                <p  >Procesando orden...</p>
            </div>



        </div>
      ) : '' )
  };

  const onMethodChanged = (e) => {
    SetMethodSelected(parseInt(e.currentTarget.value));
};

  return (
    <section className="naturalInner">
      <div className="container">
        <div className="row">
          <div className="headingOther">
            <h2>PRODUCTOS EN EL<span> CARRITO</span></h2>
          </div>
        </div>
        <div className="row">
          <div className="cart-back table-responsive">
            <table className="table table-bordered table-cont2">
              <thead>
              <tr>
                <th className="th">Imagen</th>
                <th className="th">Producto</th>
                <th className="th">Cantidad</th>
                {/* <th className="th">Precio</th> */}
              </tr>
              </thead>
              <tbody>
              {
                cart.map((product,index) => {
                  //  console.log(product);
                  return (
                    <tr key={index}>
                      <td align="center"><img className="img-responsive" style={{height:40}} src={product.id > 9999 ? product.image :  BASE_URL + product.image} alt=""/></td>
                      <td align="center">{product.name}</td>

                      <td align="center">{product.count + ' ' + product.unit} </td>
                      {/* <td align="center">{formatter.format(product.productSelectedPrice)} </td> */}
                    </tr>
                  )
                })
              }

              </tbody>
            </table>
            <div className='row'>
              <form className='form-inline'>
                <div className='col-md-4'>
                  <input type="text" ref={comments} className="form-control" placeholder="Comentarios" style={{width:'100%'}}/>
                </div>
                {/* <div className='col-md-4'>
                  <select className="form-control" placeholder="Comentarios" style={{width:'100%'}} value={methodSelected} onChange={onMethodChanged}>
                    <option value={11} >Selecciona un método de pago</option>
                    <option value={1} >Tarjeta de débito/crédito</option>
                    <option value={0} > Efectivo</option>
                  </select>
                </div> */}
                <div className='col-md-4'>
                  <div className="form-group">
                    <DatePicker
                      min={dateNow}
                      locale="es"
                      className="form-control"
                      inputFormat="D-M-YYYY"
                      input={true}
                      placeholder="Fecha entrega"
                      isGregorian={true}
                      value={value}
                      onChange={value => setValue(value)} />
                  </div>
                </div>
                <div className='col-md-2'>
                  <BtnCart/>
                </div>
              </form>
            </div>
            <div className='row'>
              <p style={{float:'right', paddingTop:10, paddingRight:10}}>Nota: Por favor verifica que tu orden esté completa antes de "levantar pedido".</p>
            </div>
            {/* <div className='row' style={{float:'right', paddingTop:10, paddingRight:10}}>
              <p>Subtotal: {formatter.format(cartTotal)}</p>
              <p>Costo de envío: {formatter.format(SHIPPING_COST)}</p>
              <p>Total: {formatter.format(Number(cartTotal) + Number(SHIPPING_COST))}</p>
            </div> */}
          </div>
        </div>

      </div>
    </section>
  )
};

export default withRouter(Cart);
